<template lang="pug">
  v-container
    v-row
      v-col.col-12.col-lg-4
        v-card
          v-card-title YouTube 360
          v-card-text
            p This demo explores the <code>handsfree.head.morphs</code> properties to match an emoji to your face!
            h3.mb-3 How to use
            p Create different emojis by making different faces: üòê üôÇ üòÆ üò≤ üòâ üò° 
      v-col.col-12.col-lg-8
        v-card
          v-card-text
            h1#emoji(:style='emojiStyles') {{emoji}}
</template>

<script>
export default {
  data: () => ({
    headPOV: [],
    emoji: 'üòÄ'
  }),

  computed: {
    emojiStyles() {
      return `transform: perspective(1000px) rotateX(${
        this.headPOV[0]
      }rad) rotateY(${this.headPOV[1]}rad) rotateZ(${this.headPOV[2]}rad)`
    }
  },

  mounted() {
    window.Handsfree.disable('vertScroll')
    window.Handsfree.use('emojify', (pointer, instance) => {
      // Map the head rotation
      this.$set(this.headPOV, 0, -instance.head.rotation[0])
      this.$set(this.headPOV, 1, instance.head.rotation[1])
      this.$set(this.headPOV, 2, -instance.head.rotation[2])

      /*
        0: smileRight ‚Üí closed mouth smile right
        1: smileLeft ‚Üí closed mouth smile left
        2: eyeBrowLeftDown ‚Üí eyebrow left frowned
        3: eyeBrowRightDown ‚Üí eyebrow right frowned
        4: eyeBrowLeftUp ‚Üí eyebrow left up (surprised)
        5: eyeBrowRightUp ‚Üí eyebrow right up (surprised)
        6: mouthOpen ‚Üí mouth open
        7: mouthRound ‚Üí mouth round
        8: eyeRightClose ‚Üí close right eye
        9: eyeLeftClose ‚Üí close left eye
        10: mouthNasty ‚Üí mouth nasty (upper lip raised)
       */
      const morphs = instance.head.morphs
      // üòÄüòÇüòâüò•üòÆüòØüòíüòîüò≤üòßüòäüòöü§®üòêüòèüòØüò°ü§¢
      let emoji = 'üòê'
      let thresholdLow = 0.25
      let threshold = 0.65
      let thresholdHigh = 0.8
      let isHappyLips = morphs[0] > threshold && morphs[1] > threshold
      let isSmirkingLips =
        (morphs[0] > thresholdHigh && morphs[1] < thresholdHigh) ||
        (morphs[0] < thresholdHigh && morphs[1] > thresholdHigh)
      let isMehLips = morphs[0] < threshold && morphs[0] < threshold
      let isOppositeBrow =
        (morphs[2] > thresholdHigh && morphs[5] > thresholdHigh) ||
        (morphs[3] > thresholdHigh && morphs[4] > thresholdHigh)
      let isBothBrowsDown = morphs[2] > thresholdLow && morphs[3] > thresholdLow
      let isBothBrowsUp = morphs[4] > thresholdLow && morphs[5] > thresholdLow
      let isWinking =
        (morphs[8] > thresholdHigh && morphs[9] < thresholdHigh) ||
        (morphs[8] < thresholdHigh && morphs[9] > thresholdHigh)

      if (isOppositeBrow && isMehLips && !isWinking) emoji = 'ü§®'
      if (isBothBrowsDown) emoji = 'üò°'
      if (morphs[6] > thresholdLow) emoji = 'üòÆ'
      if (isBothBrowsUp && morphs[6] > thresholdLow) emoji = 'üò≤'
      if (isHappyLips) emoji = 'üôÇ'
      if (isHappyLips && isBothBrowsUp) emoji = 'üòÄ'
      if (isWinking && (isHappyLips || isSmirkingLips)) emoji = 'üòâ'

      this.emoji = emoji
    })
  },

  beforeDestroy() {
    window.Handsfree.disable('emojify')
    window.Handsfree.enable('vertScroll')
  }
}
</script>

<style lang="sass" scoped>
#emoji
  font-size: 256px
  color: #000
  text-align: center
  margin-top: 150px
  margin-bottom: 150px
</style>